* {
  box-sizing: border-box;
}

body {
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell,
    'Open Sans', 'Helvetica Neue', sans-serif;
  background-color: #dfd5d5;
  color: #212121;
  line-height: 1.5;
}

#search-box {
  font: inherit;
  padding: 8px;
  max-width: 320px;
  margin-bottom: 16px;
}

li {
  list-style: none;
}

ul {
  list-style: none;
  padding: 0;
}

.render-country {
  display: flex;
  align-items: center;
}

.name-country {
  margin-left: 20px;
  font-size: 20px;
  font-weight: 700;
}
.header-name {
  font-size: 30px;
  font-weight: 900;
  margin-left: 20px;
}

.header-country {
  padding: 0;
  display: flex;
  align-items: center;
}

.text-country {
  font-size: 20px;
  font-weight: 300;
  margin-top: 10px;
}
